@import "../scss/variables";

ul.resp-tabs-list {
	margin: 0px;
	padding: 0px;
}

.resp-tabs-list li {
	font-weight: 600;
	font-size: 13px;
	display: inline-block;
	padding: 13px 15px;
	margin: 0 4px 0 0;
	list-style: none;
	cursor: pointer;
	float: left;
}

.resp-tabs-container {
	padding: 0px;
	background-color: transparent;
	clear: left;
}

h2.resp-accordion {
	cursor: pointer;
	padding: 5px;
	display: none;
}

.resp-tab-content {
	display: none;
	padding: 20px 20px 0 20px;
	padding-bottom: 0;
}

.resp-tab-active {
	border: 1px solid #8645ff !important;
	border-bottom: none;
	margin-bottom: 0px !important;
	padding: 12px 14px 14px 14px !important;
	border-bottom: 0 !important;
	border-bottom: none;
	background-color: $white;
}

.resp-content-active,
.resp-accordion-active {
	display: block;
}

.resp-tab-content {
	border: 1px solid #c1c1c1;
	border-top-color: #8645ff;
}

h2 {
	&.resp-accordion {
		font-size: 13px;
		border: 1px solid #c1c1c1;
		border-top: 0px solid #c1c1c1;
		margin: 0px;
		padding: 10px 15px;
	}
	&.resp-tab-active {
		border-bottom: 0px solid #c1c1c1 !important;
		margin-bottom: 0px !important;
		padding: 10px 15px !important;
	}
	&.resp-tab-title:last-child {
		border-bottom: 12px solid #c1c1c1 !important;
		background: blue;
	}
}


/*-----------Vertical tabs-----------*/

.resp-vtabs {
	ul.resp-tabs-list {
		float: none;
		margin-top: 0 !important;
		color: $white;
		padding-top: 14px;
	}
	.resp-tabs-list li {
		padding: 13px 13px !important;
		margin: 0 0 4px;
		cursor: pointer;
		float: none;
		margin: 0 auto;
		text-align: center;
		background-color: transparent;
		border-left: 0 !important;
		border: 0 !important;
		line-height: 0;
		transform: rotate(45deg);
	}
	.resp-tabs-container {
		padding: 0px;
		background-color: transparent;
		clear: none;
		border-bottom: 0 !important;
	}
	.resp-tab-content {
		border: none;
		word-wrap: break-word;
		height: 100%;
	}
	li {
		&.resp-tab-active {
			position: relative;
			margin: 0 auto;
			text-align: center;
			border-top: 1px solid;
			border-right: 0 !important;
			.side-menu__icon {
				border: 1px solid rgb(74, 145, 251) !important;
				background: #0355d0;
				box-shadow: 0 0px 5px 0 rgba(19, 214, 249, 0.6), 0 2px 10px 0 rgba(0, 184, 217, 0.6);
			}
		}
		&.active {
			border-right: 0 !important;
			color: $white;
		}
	}
	.resp-tab-active:hover {
		border-right: 0 !important;
		color: $white;
	}
}

.resp-arrow {
	width: 0;
	height: 0;
	float: right;
	margin-top: 3px;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 12px solid #c1c1c1;
}

h2.resp-tab-active {
	span.resp-arrow {
		border: none;
		border-left: 6px solid transparent;
		border-right: 6px solid transparent;
		border-bottom: 12px solid #9B9797;
	}
	background: #DBDBDB;
	/* !important;*/
}


/*-----------Accordion styles-----------*/

.resp-easy-accordion {
	h2.resp-accordion {
		display: block;
	}
	.resp-tab-content {
		border: 1px solid #c1c1c1;
		&:last-child {
			border-bottom: 1px solid #c1c1c1;
			/* !important;*/
		}
	}
}

.resp-jfit {
	width: 100%;
	margin: 0px;
}

.resp-tab-content-active {
	display: block;
}

h2.resp-accordion:first-child {
	border-top: 1px solid #c1c1c1;
	/* !important;*/
}